.Dialog {
  &-container {
    & .hb-modal-content {
      padding: 20px;
    }
  }
  &-body {
    padding: 40px 0 0;
  }
  &-title {
    margin-bottom: 24px;
    color: var(--nightblue-100);
    font-size: 27px;
    font-weight: 500;
    line-height: 31px;
    text-align: center;
    letter-spacing: -0.01em;
  }
  &-desc {
    margin: 0;
    color: var(--nightblue-100);
    font-size: 18px;
    line-height: 23px;
    text-align: center;
    letter-spacing: -0.02em;
  }
  &-actions {
    margin: 16px 0 24px;
    display: flex;
    flex-direction: column;
    align-items: center;
    & > button {
      width: 100%;
      height: 52px;
      font-size: 17px;
      justify-content: center;
      border-radius: 16px;
    }
    & > span {
      color: var(--petrol-80);
      font-size: 17px;
      font-weight: 500;
      line-height: 20px;
      cursor: pointer;
      &.margin {
        margin-top: 20px;
      }
    }
  }
  &-textArea {
    margin: 24px 0 0;
    color: var(--petrol-100);
    font-size: 15px;
    font-weight: 500;
    background: var(--petrol-5);
  }
}
