.AllAppointmentsLayout {
  &-area {
    @media screen and (max-width: 480px) {
      margin: 0;
    }
    &.filter {
      & .btnFilter {
        background: var(--aquamarin-60);
        border-color: var(--aquamarin-80);
        color: var(--petrol-100);
      }
    }
  }
  &-header {
    margin: 28px 0 26px;
    min-height: 36px;
    display: flex;
    justify-content: space-between;
    @media screen and (max-width: 480px) {
      margin: 20px;
    }
    & > button {
      & > i {
        margin-right: 10px;
        font-size: 20px;
      }
      &:nth-child(2) > i {
        margin: 0 0 0 10px;
      }
    }
  }
  &-wrapperContainer {
    border-radius: 30px;
    display: flex;
    flex-direction: column;
    @media screen and (max-width: 860px) {
      padding: 30px 0;
    }
  }
  &-container {
    margin-top: 60px;
    width: 100%;
    &.bg {
      padding: 30px;
      min-height: 87px;
      background: var(--petrol-5);
      border: 1px solid #fff;
      border-radius: 36px;
      box-shadow: 5px 0px 20px rgba(21, 34, 79, 0.1);
    }
    @media screen and (max-width: 860px) {
      padding-top: 54px;
      margin-top: 0;
      &.bg {
        margin-top: 40px;
      }
    }
    @media screen and (max-width: 480px) {
      padding: 30px 20px 0;
      &.bg {
        padding: 30px 20px;
      }
    }
  }
  &-title {
    margin: 0;
    color: #fff;
    font-size: 34px;
    line-height: 38px;
    text-align: center;
    letter-spacing: -0.01em;
    word-break: break-word;
    @media screen and (max-width: 480px) {
      margin: 0 20px;
      font-size: 31px;
    }
  }
  &-desc {
    margin-bottom: 30px;
    color: var(--nightblue-100);
    font-size: 18px;
    line-height: 25px;
    letter-spacing: -0.02em;
    @media screen and (max-width: 860px) {
      margin-bottom: 16px;
    }
  }
  &-list {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    & > div {
      margin-top: 30px;
      width: calc(50% - 15px);
      &:first-child,
      &:nth-child(2) {
        margin-top: 0;
      }
      @media screen and (max-width: 860px) {
        margin-top: 16px;
        width: 100%;
        &:nth-child(2) {
          margin-top: 16px;
        }
      }
    }
  }
  &-loadmore {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    & .hb-btn {
      margin-top: 20px;
      padding: 10px 20px;
    }
  }
  &-loading {
    margin: 0;
  }
  &-noResults {
    width: 100% !important;
    & > p {
      margin: 0;
      color: var(--petrol-100);
      font-size: 18px;
      line-height: 25px;
      letter-spacing: -0.02em;
    }
    &_footer {
      display: flex;
      margin-top: 10px;
      @media screen and (max-width: 480px) {
        justify-content: center;
      }
      & > button {
        min-width: 200px;
        padding: 12px 15px;
        height: auto;
        font-size: 14px;
        border-radius: 12px;
        justify-content: center;
        @media screen and (max-width: 480px) {
          width: 100%;
          max-width: 360px;
        }
      }
    }
  }
}

/**
* MAIN PAGE
*/
.AppointmentsCategories {
  &-container {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
  }
  &-bookArea {
    padding: 20px 16px;
    width: calc((100% - 30px) / 3);
    height: 202px;
    background: var(--aquamarin-60);
    border-radius: 24px;
    @media screen and (max-width: 860px) {
      margin-bottom: 20px;
      width: 100%;
    }
    &.assistant {
      background: var(--gradient-petrol-light);
    }
    &_content {
      margin-bottom: 8px;
      display: flex;
      justify-content: space-between;
      align-items: center;
      @media screen and (max-width: 860px) {
        padding: 0 15px;
      }
      @media screen and (max-width: 480px) {
        padding: 0;
      }
      & > p {
        margin: 0;
        width: 50%;
        color: var(--nightblue-100);
        font-size: 18px;
        line-height: 23px;
        letter-spacing: -0.02em;
      }
      & > img {
        width: 50%;
        height: 109px;
        object-fit: cover;
      }
      @media screen and (max-width: 860px) {
        & > img {
          width: 132px;
        }
      }
    }
    & > .btn {
      width: 100%;
      height: 45px;
      font-size: 15px;
      line-height: 20px;
      background: var(--violet-80);
      border-radius: 12px;
      justify-content: center;
      &:hover {
        background: var(--violet-100);
      }
    }
  }
  &-list {
    width: calc(2 * (100% - 30px) / 3);
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    @media screen and (max-width: 860px) {
      width: 100%;
    }
    & .CategoryItem {
      &-container {
        margin-top: 30px;
        width: calc(50% - 15px);
        &:first-child,
        &:nth-child(2) {
          margin-top: 0;
        }
        @media screen and (max-width: 860px) {
          margin-top: 20px;
          width: 100%;
          &:nth-child(2) {
            margin-top: 20px;
          }
        }
      }
    }
  }
}

/**
* Filter
*/
.Filter {
  &-area {
    display: flex;
    justify-content: center;
  }
  &-container {
    width: 100%;
    max-width: 680px;
  }
  &-lb {
    margin-bottom: 8px;
    color: var(--nightblue-100);
    font-size: 19px;
    font-weight: 500;
    &.small {
      font-size: 15px;
    }
  }
  &-chooseDate {
    margin-bottom: 15px;
    padding-bottom: 15px;
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    border-bottom: 1px solid var(--petrol-10);
    @media screen and (max-width: 860px) {
      margin-bottom: 30px;
      padding-bottom: 30px;
    }
    & > div {
      width: calc(50% - 15px);
      @media screen and (max-width: 860px) {
        width: 100%;
      }
    }
  }
  &-datePicker {
    & .FormGroupDatePicker {
      &-area {
        margin: 0;
      }
      &-container {
        & > input {
          height: 51px;
        }
        & .hb-datepicker {
          width: 51px;
          height: 51px;
        }
      }
    }
    @media screen and (max-width: 860px) {
      margin-top: 30px;
    }
  }
  &-timesOptions {
    display: flex;
    align-items: center;
    & > div {
      margin-bottom: 0;
      flex-grow: 1;
    }
    & > span {
      margin: 0 16px;
      color: var(--nightblue-100);
      font-size: 13px;
    }
  }
  &-options {
    & > button {
      height: 51px;
      font-size: 17px;
      border-radius: 16px;
    }
  }
  &-actions {
    margin-top: 30px;
    display: flex;
    flex-direction: column;
    align-items: center;
    & > button {
      width: 100%;
      height: 45px;
      font-size: 15px;
      justify-content: center;
      border-radius: 12px;
    }
    & > span {
      margin-top: 15px;
      color: var(--petrol-80);
      font-size: 15px;
      font-weight: 500;
      cursor: pointer;
      &:hover {
        color: var(--aquamarin-100);
      }
    }
  }
}

/**
* Edit
*/
.Edit {
  &-area {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
  }
  &-info {
    width: 360px;
    @media screen and (max-width: 860px) {
      margin-bottom: 30px;
      width: 100%;
    }
  }
  &-content {
    padding: 30px;
    width: calc(100% - 390px);
    background: var(--petrol-5);
    border: 1px solid #fff;
    border-radius: 30px 30px 0 0;
    @media screen and (max-width: 860px) {
      width: 100%;
    }
  }
  &-schedule {
    & .SetSchedule {
      &-chooseDate {
        @media screen and (max-width: 1120px) {
          margin-bottom: 30px;
          padding-bottom: 30px;
          & > div {
            width: 100%;
          }
        }
      }
      &-datePicker {
        @media screen and (max-width: 1120px) {
          margin-top: 15px;
        }
      }
    }
  }
}
